// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import {
  BootstrapVue,
  IconsPlugin
} from 'bootstrap-vue'
import './bus'
import store from './store'
//import './assets/mobile/pttweb.css'
import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'

Vue.config.productionTip = false
Vue.use(BootstrapVue)
Vue.use(IconsPlugin)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: {
    App
  },
  computed: {
    getAccountLabel: function () {
      /**
       * * Original 'Charmon' length = 7
       * * Rest of space length = 9, so is 16 in total
       */
      var t = this.account == '' ? 'Guest' : this.account;
      t = t.padEnd(16);
      return t;
    },
    getTimeLabel: function () {
      /**
       * * Original is '[9/30  0:29]' length = 15
       */
      var now = new Date();
      var now_local = now.toLocaleDateString();
      var mon = now_local.split('/')[0];
      var day = now_local.split('/')[1];
      var day_list = ['', '', '', '', '', '', ''];
      var week = day_list[now.getDay()];
      var hour = now.getHours();
      var min = now.getMinutes();
      var t = '[' + mon + '/' + day + ' ' + week + ' ' + hour + ':' + min + ']';
      t = t.padStart(15);
      return t;
    }
  },
  created: function () {
    /**
     * * Vue
     */
    window.addEventListener('keyup', this.onKeyup); // ! 
    window.addEventListener('resize', this.onResize); // ! 
    // ! login.js
    this.$bus.$on("try-login", (i) => {
      this.checkLogin(i[0], i[1]);
    });
  },
  watch: {
    '$route'(to, from) {
      /**
       * * router view
       */
      this.$store.commit('setAccount', this.getAccountLabel); // ! Label
      this.$store.commit('setDate', this.getTimeLabel); // ! Label
    }
  },
  template: '<App/>',
  data: {
    account: '',
    password: ''
  },
  methods: {
    checkLogin: function (id, pw) {
      this.account = id;
      this.password = pw;
    },
    onKeyup: function (e) {
      //if (this.$route.name == 'Login') return;

      var now = this.$store.state.rowIndex;
      var length = this.$store.state.rowCount;
      //console.log(now+" -> "+length);
      switch (e.which) {
        case 37: // * left
          if (this.$route.name != 'MainMenu') {
            router.go(-1);
          }
          break;
        case 38: // * up
          now--;
          if (now < 0) now = length - 1;
          break;
        case 39: // * right
          if (this.$route.name != 'Login') {
            this.enterPage(now);
          }
          break;
        case 40: // * down
          now++;
          if (now > length - 1) now = 0;
          break;
        case 13: // * enter
          this.enterPage(now);
          break;
        default:
          console.log(e.which);
          return;
      }
      this.$store.commit('setRowIndex', now);
      // ! 
      this.$bus.$emit('on-keyup', e);
      e.preventDefault();
    },
    enterPage: function (i) {
      console.log("enter index:" + i);
      var from = this.$route.name;
      var to = '';
      console.log("from:" + from + " to:" + to);

      if (from == 'Login') {
        if (this.account != "" &&
          this.password != "") {
          console.log("success!");
          // ! MainMenu
          this.$router.push({
            name: 'MainMenu',
          })
        } else {
          console.log("failed");
        }
        return;
      }

      if (from == 'MainMenu') {
        switch (i) {
          case 0:
            this.$router.push({
              name: 'Favorite',
            })
            break;
          case 1:
            this.$router.push({
              name: 'Class',
            })
            break;
          case 2:
            this.$router.push({
              name: 'Mail1',
            })
            break;
          case 3:
            this.$router.push({
              name: 'Goodbye',
            })
            break;
        }
        return;
      }

      if (from == 'Goodbye') {
        this.$router.push({
          name: 'Login',
        })
        return;
      }
    },
    onResize: function (e) {
      var w = window.innerWidth;
      var h = window.innerHeight;
      this.$store.commit('setWindowSize', [w, h]);
    }
  }
})



